<template>
    <div class="tip" :class="{ isDefective }">
        <div class="fileUrl">
            <el-button class="fileUrl" type="text" @click="toUrl({ line: 0, start: 0, end: 0 })">{{ fileUrl }}</el-button>
            <span class="notDefectiveTip" v-if="!isDefective">国际化检测正常 <i class="el-icon-success"></i></span>
        </div>
        <div class="defective" v-if="isDefective">
            <div v-for="(item, index) in list" :key="index">
                <el-button @click="toUrl(item)" class="line" type="text">未国际化的文案: ${ {{item.text}} }, 位置: 第{{ item.line }}行, {{ item.start }} 至 {{ item.end }} 字符位置</el-button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        option: Object,
    },
    data() {
        return {

        };
    },
    computed: {
        list() {
            const list = [];
            this.option.data.result.forEach(item => {
                list.push(...item);
            });
            return list;
        },

        isDefective() {
            return !!this.option.data.result.length;
        },
        fileUrl() {
            return this.option.data.fileUrl;
        },
    },
    created() {

    },
    mounted() {
        
    },
    watch: {

    },
    methods: {
        toUrl(item) {
            this.$emit("toUrl", {
                fileUrl: this.fileUrl,
                line: item.line,
                start: item.start,
                end: item.end,
            });
        }
    },
    components: {

    },
};
</script>

<style scoped lang="scss">
.tip {
    padding: 8px 16px;
    background-color: #ecf8ff;
    border-radius: 4px;
    border-left: 5px solid #50bfff;
    margin: 10px 0;

    &.isDefective {
        border-left: 5px solid #E6A23C;
    }

    .fileUrl, .line {
        padding-top: 0px;
        padding-bottom: 0px;
    }

    .notDefectiveTip {
        padding-left: 20px;
        font-size: 9px;
        color: #67C23A;
    }

    .defective {
        padding-top: 10px;
    }
}
</style>
