<template>
  <div class="content">
    <template v-for="(item, index) in infoList">
      <el-alert
        v-if="item.type === 'info'"
        :key="index"
        :title="item.message"
        type="info"
        :closable="false"
      >
      </el-alert>
      <el-alert
        v-if="item.type === 'error'"
        :key="index"
        :title="item.message"
        type="error"
        effect="dark"
        :closable="false"
      >
      </el-alert>
      <el-checkbox
        class="optionStyle"
        v-if="item.type === 'option'"
        :key="index"
        :value="item.data.is"
        >{{ item.message }}</el-checkbox
      >
      <div v-if="item.type === 'gitignoreList'" :key="index">
        <div
          class="gitignoreItem"
          v-for="(gitignoreItem, index) in item.data"
          :key="index"
        >
          {{ gitignoreItem }}
        </div>
      </div>
      <fileCheckOption
        v-if="item.type === 'checkView'"
        :key="index"
        :option="item"
        @toUrl="toUrl"
      />
    </template>
  </div>
</template>

<script>
import fileCheckOption from "./fileCheckOption.vue";

export default {
  props: {},
  data() {
    return {
      vscode: acquireVsCodeApi(),
      infoList: [],
    };
  },
  computed: {},
  created() {},
  mounted() {
    window.addEventListener("message", (event) => {
      const message = event.data;
      this.addMessageData(message);
    });

    window.addMessage = this.addMessageData;

    this.vscode.postMessage({ ok: true });
  },
  watch: {},
  methods: {
    addMessageData({ type, message, data }) {
      if (["info", "error", "option", "gitignoreList", "checkView"].includes(type)) {
        console.log("message:", { type, message, data });
        if (type === "option" && data.is) {
          this.infoList.find((item) => {
            return (item.data || {}).id === data.id && !(item.data || {}).is;
          }).data.is = true;
        } else {
          this.infoList.push({ type, message, data });
        }
      }
    },

    toUrl(data) {
      this.vscode.postMessage({ type: 1, toData: data });
    }
  },
  components: { fileCheckOption },
};
</script>

<style lang="scss">
.content {
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative;
  text-align: left;

  .optionStyle {
    padding-left: 10px;
  }

  .gitignoreItem {
    text-indent: 30px;
    color: #999;
    font-size: 9px;
  }
}
</style>
